import React from "react";

//import { PageTitle } from '../../layout-components';
import { IconButton, Box, Card, CardContent } from "@material-ui/core";
import axios from "axios";
import jsPDF from "jspdf";
import "jspdf-autotable";
import { Row, Col, Tabs, Tab, Button } from "react-bootstrap";
//import TemplatePrint from '../../Template/TemplatePrint'
import config from "../../Config/config";
import moment from "moment"
import Aux from "../../../hoc/_Aux";
import jwt_decode from "jwt-decode"

var urlbackend = require("../../../env/env")();

class ListColisPayed extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            listcolis: [],
            providerinfo: {},

        };
    }
    componentDidMount() {
        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };
        this.setState({ providerinfo: jwt_decode(localStorage.getItem('token')) })


        axios.get(urlbackend + "/factures/status/code/" + this.props.codefacture.code_facture, config)
            .then(response => {
                console.log("this colis facture list", response.data.data)
                this.setState({ listcolis: response.data.data })

            })
            .catch(e => {
                console.log(e)
            })

    }




    reloaddata() {
        this.componentDidMount()
    }

    changecolor(valeur) {
        switch (valeur) {
            case 0:
                return "#eeeeca"
            case 1:
                return "#adcadc"
            case 2:
                return "#eeeee1"
            case 3:
                return "#dcadb1"
            default:
                break;
        }

    }
    exportPDF = () => {
        const unit = "pt";
        const size = "A4"; // Use A1, A2, A3 or A4
        const orientation = "landscape"; // portrait or landscape

        // const marginLeft = 40;
        const doc = new jsPDF(orientation, unit, size);

        doc.setFontSize(15);

        const title = "RECETTE"
        const headers = [["Code colis", "Nom client", "Addresse", "N.telephone", "PRIX", "frais livraision", "status", "Montant pour fournisseur", "Numero de cheque"]];
        const headersTotal = [["Nombre colis", "Total paiement ", "frais Livraison", "encaissment"]];
        const dataTotal = [[this.props.codefacture.nb_colis, this.props.codefacture.frais_livraision, this.props.codefacture.frais_total, (this.props.codefacture.frais_livraision - this.props.codefacture.frais_total),]];

        const data = this.state.listcolis.map(elt => [elt.colis.index_colis, elt.colis.name_complete, elt.colis.gouvernorat, elt.colis.phone_number, elt.colis.price, elt.frais_livraision, (elt.delivery_status ? "livré" : "retour"), (elt.delivery_status ? elt.colis.price - elt.frais_livraision : - elt.frais_livraision), elt.numero_check]);
        let contenttotal = {
            startY: 135,
            head: headersTotal,
            body: dataTotal,
            theme: "grid"

        };
        let content = {
            startY: 180,
            head: headers,
            body: data,
            theme: "grid"

        };
        var image = new Image();
        image.src = config.images;
        doc.addImage(image, 'PNG', 600, 10, 160, 60);
        doc.text(config.matricule, 600, 90);
        doc.text(config.addresse1, 540, 115);
        doc.text(config.addresse2, 600, 130);
        doc.rect(280, 40, 2, 50, 'F');
        doc.rect(500, 40, 2, 52, 'F');
        doc.rect(280, 40, 220, 2, 'F');
        doc.rect(280, 90, 220, 2, 'F');
        // doc.rect(20, 40, 2, 50, 'F');
        // doc.rect(20, 40, 2, 52, 'F');
        // doc.rect(280, 40, 220, 2, 'F');
        // doc.rect(280, 90, 220, 2, 'F');
        doc.setFont(undefined, 'bold')



        doc.text(this.props.codefacture.code_facture, 300, 60);
        doc.text(("Date :" + new moment().format('DD -MM-YYYY  HH:mm')), 300, 80);
        doc.text(("Expéditeur :" + (this.props.codefacture.provider.username ? this.props.codefacture.provider.username : this.state.providerinfo.username)), 50, 40);
        doc.text(("N. de téléphone :" + (this.props.codefacture.provider.phone_number ? this.props.codefacture.provider.phone_number : this.state.providerinfo.phone_number)), 50, 60);
        doc.text(("Fiscal/CIN:" + (this.props.codefacture.provider.phone_number ? this.props.codefacture.provider.phone_number : this.state.providerinfo.phone_number)), 50, 100);
        doc.text(("Total:" + this.state.listcolis.length + " colis"), 50, 80);
        doc.autoTable(contenttotal);
        doc.autoTable(content);


        // doc.text(title, 100, 40);
        doc.save(this.props.codefacture.code_facture + ".pdf")
    }
    exportFacture = () => {
        const unit = "pt";
        const size = "A4"; // Use A1, A2, A3 or A4
        const orientation = "portrait"; // portrait or landscape

        // const marginLeft = 40;
        const doc = new jsPDF(orientation, unit, size);

        doc.setFontSize(10);

        const headers = [["Code colis", " client ", "PRIX", "frais livraision", "status", "Montant pour fournisseur", "Numero de cheque"]];
        const headersTotal = [["Nombre colis", "Total paiement ", "frais Livraison", "encaissment"]];
        const dataTotal = [[this.props.codefacture.nb_colis, this.props.codefacture.frais_livraision, this.props.codefacture.frais_total, (this.props.codefacture.frais_livraision - this.props.codefacture.frais_total),]];

        const data = this.state.listcolis.map(elt => [elt.colis.index_colis, elt.colis.name_complete + " " + elt.colis.gouvernorat + " " + elt.colis.phone_number, elt.colis.price, elt.frais_livraision, (elt.delivery_status ? "livré" : "retour"), (elt.delivery_status ? elt.colis.price - elt.frais_livraision : - elt.frais_livraision), elt.numero_check]);
        let contenttotal = {
            head: headersTotal,
            body: dataTotal,
            theme: "grid",
            startX: 100,
            headStyles: {
                font: "times",
                textColor: "#000000",
                lineColor: "#000000",
                fillColor: "#ccd0cb"

            },


        };
        let content = {
            startY: 135,
            head: headers,
            body: data,
            theme: "grid",
            headStyles: {
                font: "times",
                textColor: "#000000",
                lineColor: "#000000",

            },
            bodyStyles: {
                lineColor: "#000000",
            },
            alternateRowStyles: {
                color: "#000000"
            }


        };
        var image = new Image();
        image.src = config.images;
        doc.addImage(image, 'PNG', 10, 10, 130, 40);
        doc.setLineWidth(1.5);
        doc.line(20, 60, 580, 60);
        doc.rect(300, 10, 280, 45);
        doc.text("FastBox", 305, 20);
        doc.text("Tél:56 704 902 :fastbox.tn@gmail.com", 305, 30);
        doc.text(config.addresse1+config.addresse2, 305, 40);
        doc.setFontSize(9);
        doc.text(("Expéditeur :" + (this.props.codefacture.provider.username ? this.props.codefacture.provider.username : this.state.providerinfo.username)), 20, 80);
        doc.text(((this.props.codefacture.provider.address ? this.props.codefacture.provider.address : this.state.providerinfo.address)), 20, 95);
        doc.text(((this.props.codefacture.provider.code_fiscal ? this.props.codefacture.provider.code_fiscal : this.state.providerinfo.code_fiscal)), 20, 110);

        doc.text("Facture :" + this.props.codefacture.code_facture, 400, 100);
        doc.text(" ", 540, 115);
        doc.setFont(undefined, 'bold')
        doc.autoTable(content);
        doc.autoTable(contenttotal);


        // doc.text(title, 100, 40);
        doc.save(this.props.codefacture.code_facture + ".pdf")
    }
    exportFactureLivraison = () => {
        const unit = "pt";
        const size = "A4"; // Use A1, A2, A3 or A4
        const orientation = "portrait"; // portrait or landscape

        // const marginLeft = 40;
        const doc = new jsPDF(orientation, unit, size);

        doc.setFontSize(10);

        const headersTotal = [['Description', "Qte", "PU", "Montant "]];
        const dataTotal = [["Service Livraison", this.props.codefacture.nb_colis, (this.props.codefacture.frais_total / this.props.codefacture.nb_colis).toFixed(2), this.props.codefacture.frais_total], ["", " ", " TVA (19%)", this.props.codefacture.frais_total * 0.19], ["", " ", " ToTal", this.props.codefacture.frais_total * 1.19]];

        let contenttotal = {
            head: headersTotal,
            body: dataTotal,
            theme: "grid",
            startY: 150,
            headStyles: {
                font: "times",
                textColor: "#000000",
                lineColor: "#000000",
                fillColor: "#ccd0cb"

            },


        };

        var image = new Image();
        image.src = config.images;
        doc.addImage(image, 'PNG', 10, 10, 130, 40);
        doc.setLineWidth(1.5);
        doc.line(20, 60, 580, 60);
        doc.rect(300, 10, 280, 45);
        doc.text("FastBox", 305, 20);
        doc.text("Tél:56 704 902 :fastbox.tn@gmail.com", 305, 30);
        doc.text(config.addresse1+config.addresse2, 305, 40);
        doc.setFontSize(9);
        doc.setFillColor(24, 103, 244);
        doc.setDrawColor(24, 103, 244);
        doc.rect(20, 70, 90, 20, "FD");
        doc.text("FACTURÉ À", 30, 83);
        doc.setFillColor(24, 103, 244);
        doc.setDrawColor(24, 103, 244);
        doc.rect(400, 70, 200, 20, "FD");
        doc.text("N°FACTURE", 410, 83);
        doc.text("N°FACTURE", 410, 83);
        doc.setFillColor(24, 103, 244);
        doc.setDrawColor(24, 103, 244);
        doc.rect(400, 110, 200, 20, "FD");
        doc.text("Date", 410, 123);
        doc.text("Date", 410, 123);
        doc.text("Date", 410, 123);
        doc.text("Condition", 480, 123);
        doc.text("Condition", 480, 123);
        doc.text(moment(this.props.codefacture.created_at).format(("DD/MM/YYYY")) ,410, 140)
        doc.text("Paiment à réception", 480, 140);
        doc.text("FACTURÉ À", 30, 83);
        doc.text(((this.props.codefacture.provider.username ? this.props.codefacture.provider.username : this.state.providerinfo.username)), 20, 100);
        doc.text(((this.props.codefacture.provider.address ? this.props.codefacture.provider.address : this.state.providerinfo.address)), 20, 115);
        doc.text(((this.props.codefacture.provider.code_fiscal ? this.props.codefacture.provider.code_fiscal : this.state.providerinfo.code_fiscal)), 20, 130);
        doc.text(this.props.codefacture.code_facture, 400, 100);
        doc.text(" ", 540, 115);
        doc.setFont(undefined, 'bold')
        doc.autoTable(contenttotal);


        // doc.text(title, 100, 40);
        doc.save(this.props.codefacture.code_facture + ".pdf")
    }
    render() {
        return (
            <Aux>
                <Button onClick={() => this.exportPDF()}>Télecharger format RECETTE</Button>
                <Button onClick={() => this.exportFacture()}>Télecharger format Facture</Button>
                <Button onClick={() => this.exportFactureLivraison()}>Télecharger Facture Livraison</Button>
                <Row>
                    <Col>


                        <hr />
                        <Tabs defaultActiveKey="home">

                            <Tab eventKey="home" title="">
                                <Card className="card-box mb-4">

                                    <CardContent className="p-0">
                                        {/* <p>Liste colis pour Pickup</p> */}
                                        <div className="table-responsive">
                                            <Row style={{ "float": "right" }}>
                                                <Col md={12} xl={4}>
                                                    <table className="table table-dark  mb-0" >
                                                        <tr>
                                                            <th>nombre colis</th>
                                                            <th>Total</th>
                                                            <th>Total livraison</th>
                                                            <th>Encaissement</th>
                                                        </tr>
                                                        <tr>
                                                            <td>{this.props.codefacture.nb_colis} </td>
                                                            <td>{this.props.codefacture.frais_livraision} </td>
                                                            <td>{this.props.codefacture.frais_total} </td>
                                                            <td>{this.props.codefacture.frais_livraision - this.props.codefacture.frais_total}  </td>
                                                        </tr>
                                                    </table>
                                                </Col>
                                            </Row>

                                            <table className="table  table-hover text-nowrap mb-0">
                                                <thead className="table thead-light mb-0">
                                                    <tr>
                                                        <th className="text-center">Code colis</th>
                                                        <th className="text-center">Client</th>
                                                        <th className="text-center">address client</th>
                                                        <th className="text-center">status</th>
                                                        <th className="text-center">Prix</th>
                                                        <th className="text-center">frais livraision</th>
                                                        <th className="text-center">Montant pour fournisseur</th>
                                                        <th className="text-center">Numero de cheque</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {this.state.listcolis.map((pickup, index) => (
                                                        <tr key={index} >


                                                            <td className="text-center">
                                                                <div className="h-auto ">
                                                                    {pickup.colis.index_colis} {" "}
                                                                </div>
                                                            </td>
                                                            <td className="text-center">
                                                                <div className="h-auto ">
                                                                    {pickup.colis.name_complete} {" : "}{pickup.colis.phone_number}
                                                                </div>
                                                            </td>
                                                            <td className="text-center">
                                                                <div className="h-auto ">
                                                                    {pickup.colis.gouvernorat}
                                                                </div>
                                                            </td>
                                                            <td className="text-center">
                                                                <div className="h-auto ">
                                                                    {pickup.delivery_status ? " Livré" : "retour"}
                                                                </div>
                                                            </td>
                                                            <td className="text-center">
                                                                <div className="h-auto ">
                                                                    {pickup.delivery_status ? pickup.colis.price : 0}
                                                                </div>
                                                            </td>




                                                            <td className="text-center">
                                                                <div className="h-auto ">
                                                                    {pickup.frais_livraision}
                                                                </div>
                                                            </td>
                                                            <td className="text-center">
                                                                <div className="h-auto ">
                                                                    {pickup.delivery_status ? pickup.colis.price - pickup.frais_livraision : - pickup.frais_livraision}
                                                                </div>
                                                            </td>


                                                            <td>

                                                                <td>
                                                                    <div className="h-auto ">
                                                                        {pickup.numero_check}
                                                                    </div>
                                                                </td>
                                                            </td>
                                                            <td>

                                                            </td>
                                                            <td className="text-center">
                                                                <Box>
                                                                    <IconButton color="primary" size="small">
                                                                        {/* <FontAwesomeIcon icon={['fas', 'ellipsis-h']} /> */}
                                                                    </IconButton>
                                                                </Box>
                                                            </td>
                                                        </tr>
                                                    ))}
                                                </tbody>
                                            </table>



                                        </div>
                                    </CardContent>
                                </Card>
                            </Tab>
                        </Tabs>
                    </Col>
                </Row>
            </Aux>
        );
    }
}

export default ListColisPayed;
